.notifications-container {
    position: relative;
    display: inline-block;
}

.notification-counter {
    position: absolute;
    top: -10px;
    right: -13px;
    background-color: red;
    color: white;
    border-radius: 50%;
    padding: 0 5px;
    font-size: 12px;
    line-height: 1.5;
}
