.ExamResult {
  text-align: center;
  max-width: 600px;
  width: 100%;
  margin: 2rem auto;
  padding: 20px;
  background-color: #f8f9fa;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.ExamResult h4 {
  font-size: 1.6rem;
}

/* Select Components */
select {
  width: 100%;
  padding: 8px;
  margin: 10px 0;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
}
.dark .ExamResult {
  background-color: #2c2c2c;
}
/* Table Styles */
table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

th,
td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

th {
  background-color: #007bff;
  color: white;
}

tr:nth-child(even) {
  background-color: #f2f2f2;
}

tr:hover {
  background-color: #ddd;
}

/* No Results Message */
.no-results {
  color: #999;
  margin-top: 20px;
}

/* Container for the selectors */
.select-container {
  margin-bottom: 20px;
}
.dark select{
  color: white;
  background-color: #2c2c2c;
}